 let graceChecker = require("../../../static/js/graceChecker.js");
    import {
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[]
			}				
		},
        computed: mapState(['hasLogin','userInfo']),
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
        methods: {  
			...mapMutations(['setUserInfo']),
            async loadData() {  
                if (!this.hasLogin) {  
					uni.redirectTo({
						url:'../login',
					})
                }else{
					this.memberInfo = this.userInfo;

				}
            },
			formSubmit: function (e) {
				var _this =this;
				var formData = e.detail.value;
				console.log(formData);
				var rule = [
					{name:"username", checkType : "string", checkRule:"2,8",  errorMsg:"昵称必须为2-8个字符"}
				];
				var checkRes = graceChecker.check(formData, rule);
				if(!checkRes){
					uni.showToast({ title: graceChecker.error, icon: "none" });
					return false;
				}
				console.log('-----check-----------');
				_this.$Request.post('member_account/editnick',{nickname:formData.username,client:'wap'}).then(res => {
					if (res.code == 200) {
						console.log(res);
						let user_info = _this.userInfo;
						user_info.nickname = formData.username;
						_this.setUserInfo(user_info);
						uni.showToast({
							icon: 'none',
							title:'修改成功！',
							duration:2000,
							success: function () {
								 setTimeout(function () {
									uni.redirectTo({
										url:'userset',
									});
								 }, 2000);
							}
						});
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000
						});
						_this.logining = false;
					}
				}).catch(function (err) {
					console.log(err);
				  });
			}
        }
    }
